<template>
  <div>
    <Ad />
    <el-row>
      <el-col :md="8" v-for="item in list" :key="item.id">
        <ChartBox :id="item.id" :color="item.color"/>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import ChartBox from './component/ChartBox'
import Ad from './component/Ad'
export default {
  components: {
    ChartBox,
    Ad
  },
  data () {
    return {
      list: Array(12).fill().map((item, i) => {
        let red = `${Math.floor(Math.random() * 255)}`
        let green = `${Math.floor(Math.random() * 255)}`
        let blue = `${Math.floor(Math.random() * 255)}`
        return {
          id: `_${i}`,
          color: `${red},${green},${blue}`
        }
      })
    }
  }
}
</script>