<template>
  <div class="chart_line">
    <canvas :id="id" height="100" style=""></canvas>
  </div>
</template>
<script>
import Chart from 'chart.js'
import moment from 'moment'
// http://gonowex.com/dataTemp/Json/Chain/Line/15min/BTC.json
export default {
  props: {
    id: {
      type: String
    },
    color: {
      type: String
    }
  },
  mounted () {
    let balance_chart = document.getElementById(this.id).getContext('2d')
    let Labels = []
    let Data = []
    
    let data = Array(50).fill().map(() => ({
      id: 1591421400,
      open: Math.random() * 1000 + 500,
    }))
    data.forEach(item => {
      Labels.push(moment(item.id * 1000).format('YYYY-MM-DD HH:mm:ss'))
      Data.push(item.open)
    })
    let Color = this.color
    let balance_chart_bg_color = balance_chart.createLinearGradient(0, 0, 0, 70)
    balance_chart_bg_color.addColorStop(0, 'rgba('+Color+',.5)')
    balance_chart_bg_color.addColorStop(1, 'rgba('+Color+'0,0)')
    new Chart(balance_chart, {
        type: 'line',
        data: {
            labels: Labels.slice(-20),
            datasets: [{
                label: 'BTC',
                data: Data.slice(-20),
                backgroundColor: balance_chart_bg_color,
                borderWidth: 3,
                borderColor: 'rgba('+Color+',1)',
                pointBorderWidth: 0,
                pointBorderColor: 'transparent',
                pointRadius: 3,
                pointBackgroundColor: 'transparent',
                pointHoverBackgroundColor: 'rgba('+Color+',1)',
            }]
        },
        options: {
            layout: {
                padding: {
                    bottom: -1,
                    left: -1
                }
            },
            legend: {
                display: false
            },
            scales: {
                yAxes: [{
                    gridLines: {
                        display: false,
                        drawBorder: false,
                    },
                    ticks: {
                        beginAtZero: false,
                        display: false
                    }
                }],
                xAxes: [{
                    gridLines: {
                        drawBorder: false,
                        display: false,
                    },
                    ticks: {
                        display: false
                    }
                }]
            },
        }
    })
  }
}
</script>
<style lang="less" scoped>
.chart_line {
  width: 100%;
  width: 400px;
  height: 110px;
  overflow: hidden;
  .text_box {
    margin-top: 500px;
    img {
      width: 150px;
      height: 150px;
      border-radius: 100%;
    }
  }
}
</style>